.make {
    position: fixed;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    margin-top: 10px;
    width: min(70vw, 600px);
    z-index: 10;

    border-radius: 10px;
    background-color: var(--light-green);

    padding: 35px;
}

/* close button for Create Note pop-up */
.make-exit {
    top: 0;
    right: 0;
    position: absolute;
    margin: 10px;

    cursor: pointer;

    border: 2px solid red;
    background-color: var(--light-green);
    color: red;
    font-size: 20px;

    border-radius: 5px;
}

.make-exit:hover {
    background-color: red;
    color: white;
}

/* header/title for Make New Note/Temp */
.make-header {
    font-size: 2rem; 
    margin-top: 5px;
    margin-bottom: 5px;
    text-align: center;   
}

/* wrapper for element where user can decide whether to make a note or a temp */
.maketxt-wrap {
    display: flex;
    align-items: center;
}

/* txt specifcially for the "New: " */
.make-text {
    font-size: 1.5rem;
    top: 0;
    left: 0;

    word-wrap: inherit;
    display: block;
    margin-bottom: 10px;
}

/* button indicating whether new note or temp */
.make-ddown-btn {
    font-size: 1.5rem;
    cursor: pointer;

    max-height: 2rem;
    
    position: flex;
    margin-bottom: 0px;
    min-width: 7rem;

    border: 1px solid black;
    text-align: center;
}

.make-ddown-btn:hover {
    background-color: var(--lightest-gray);
}

/* button that appears on hover of ddown. Make on click in future */
.ddown-temp {
    font-size: 1.3rem;
    cursor: pointer;

    max-height: 2rem;
    
    position: flex;
    margin-bottom: 0px;

    border: 1px solid black;
}

.d-arrow {
    font-size: 1rem;
}

.name-input {
    background-color: var(--lightest-gray);
    border-radius: 7px;
    font-size: 1.5rem;
    font-family: "Fira Code";
    width: 100%;
    margin-left: 1.5rem;
    margin-top: 10px;
}

.name-input:user-valid {
    outline-color: green;
    outline-width: 1px;
    outline-style: solid;
}

.name-input:user-invalid {
    outline-color: red;
    outline-width: 1px;
    outline-style: solid;
}


.template-input {
    font-size: 1.5rem;
    font-family: "Fira Code";
    background-color: var(--lightest-gray);
    border-radius: 7px;
    margin-left: 1.5rem;
    width: 100%;
    margin-top: 10px;
    padding-left: 10px;
}

.template-input:hover {
    cursor: pointer;
    background-color: var(--lighter-gray);
}

.create-button {
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    font-family: "Fira Code";
    font-size: 1.5rem;
    background-color: var(--dark-green);
    border-color: var(--dark-green);
    color: var(--offbase-white);
    padding: 2px 45px;
    border-radius: 7px;
}

.create-button:hover {
    cursor: pointer;
    background-color: var(--darker-green);
}


.delete-button {
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    font-family: "Fira Code";
    font-size: 1.5rem;
    background-color: rgba(255, 0, 0, 0.673);
    border-color: rgba(255, 0, 0, 0.673);
    color: var(--offbase-white);
    padding: 2px 45px;
    border-radius: 7px;
}

.delete-button:hover {
    cursor: pointer;
    background-color: rgb(211, 0, 0);
}